import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, ModalController, LoadingController } from 'ionic-angular';
import { BackProvider } from '../../providers/back/back';
import { AuthProvider } from '../../providers/auth/auth';
import { Content, Collections } from '../../app/app.models';
import { File } from '@ionic-native/file';
import { FileTransfer } from '@ionic-native/file-transfer';

@IonicPage({ segment: 'clase/:id/:type' })
@Component({
  selector: 'page-clase-detail',
  templateUrl: 'clase-detail.html',
})
export class ClaseDetailPage {

  @ViewChild('zamnaPlayer')
  public video: any

  public clase: Content;
  public comentarios: any[]
  public resultados: any[] = []

  public type: string = this.navParams.get('type')
  public id: string = this.navParams.get('id')

  public newComentario: string = ''
  public lc: boolean = false;

  public debug: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private back: BackProvider,
    private toast: ToastController,
    private modal: ModalController,
    private data: AuthProvider,
    private file: File,
    private transfer: FileTransfer,
    private load: LoadingController,
  ) {
  }

  ionViewDidLoad() {

    this.loadClase(this.id);

    this.back.getComentarios(this.id, this.type).subscribe(data => this.comentarios = data.reverse())
    this.back.getResultados(this.type, this.back.uid, this.id).subscribe(data => {
      this.resultados = data ? data : []
    })

  }

  openModal(id: string, type: string, modal: string) {
    const m = this.modal.create(modal, {id, type})
    m.present()
  }

  openExamen(type, id) {
    if (!this.resultados || this.resultados.length <= 0) {
      const m = this.modal.create('ExamenDetailPage', {type, id})
      m.present()
      m.onDidDismiss(data => {
        if (!data) return
        this.back.getResultados(this.type, this.back.uid, this.id).subscribe(data => this.resultados = data ? data : [])
      })
    } else {
      const m = this.modal.create('ResultadosExamenPage', {type, id, user: this.back.uid})
      m.present()
    }
  }

  deleteResultados(type, id) {
    this.back.deleteResultados(type, this.back.uid, id).subscribe(() => {
      this.ionViewDidLoad()
    })
  }

  sendComentario(text: string) {
    if (!text) return
    this.lc = true
    this.back.addComentario(this.type, this.id, this.back.uid, text).subscribe(() => {
      this.ionViewDidLoad()
      this.newComentario = ''
      this.lc = false
    }, () => {
      this.toast.create({message: 'No se pudo agregar tu comentario...', duration: 2000}).present()
      this.lc = false
    })
  }

  goto(time: any) {
    const horas = parseInt(time.HH) * 3600
    const minInSecs = parseInt(time.mm) * 60
    const t = parseInt(time.ss) + minInSecs + horas
    this.video.nativeElement.currentTime = t;
  }

  async loadClase(id: string) {
    this.clase = await this.data.getDoc<Content>(Collections.CONTENT, id)
  }


  async downloadVideo() {
    try {

      const l = this.load.create({content: 'Descargando clase, por favor no cierres la aplicación'});
      l.present();

      const fileTransfer = this.transfer.create();

      fileTransfer.download(this.clase.video, `${this.file.dataDirectory}/.zamna-cache/${this.clase.id}`).then(entry => {
        this.debug = entry;
        localStorage.setItem(`offline-${this.clase.id}`, JSON.stringify(this.clase));
        l.dismiss();
        this.toast.create({message: 'La clase se descargó correctamente, la puedes ver desde la página de descargas.', duration: 2000}).present()
      }).catch(error => {
        this.toast.create({message: 'Ocurrió un error al descargar la clase...'});
        this.debug = error;
      });

      fileTransfer.onProgress(e => this.downloadProgress(e));

    } catch (error) {
      this.toast.create({message: 'Ocurrió un error al descargar la clase...'});
      this.debug = error;
    }
  }

  downloadProgress(e: ProgressEvent) {
    console.log(e);
    this.debug = e;
  }

  async listDir() {
    try {
      await this.file.checkDir(this.file.dataDirectory, '.zamna-cache')
      this.debug = {message: '.zamna-cache dir exists'};
      const list = await this.file.listDir(this.file.dataDirectory, '.zamna-cache');
      this.debug = list;
    } catch (error) {
      this.debug = error;
    }
  }



}



